import type { ImageData } from '../../mo/models';

export default (outline: ImageData['outline']) => {
  if (!(outline && outline.width) || (outline && outline.type === 'none')) {
    return undefined;
  }

  const width = Math.round(outline.width);
  let { type } = outline;
  const color = outline.color || '#000000';
  if (type !== 'dotted' && type !== 'dashed') {
    type = 'solid';
  }
  return `${width}px ${type} ${color}`;
};
